import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})
export class CommonUtilityServices {
    constructor(private http:HttpClient) {}
    apiUrl = 'https://coronavirus-19-api.herokuapp.com/';
    apiIndia = "https://api.covid19india.org/data.json";
    apiDistrict = 'https://api.covid19india.org/state_district_wise.json';

    getGlobalData(){
        return this.http.get(this.apiUrl + 'all');
    }

    getCountryData() {
        return this.http.get(this.apiUrl + 'countries');
    } 
    getIndiaData(){
        return this.http.get(this.apiIndia);
    }
    getDistrict(){
        return this.http.get(this.apiDistrict);
    }
}