import { Component, OnInit } from '@angular/core';
import { CommonUtilityServices } from '../common.utility.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  constructor(private utility:CommonUtilityServices) { }
  globalData:any;
  totalCases:number;
  totalDeaths:number;
  totalRecoverd:number;
  indiaData: any;
  recoverPer: number;
  deathPer:number;
  allCountry:any = [];
  indiaTotalCases:any;
  statewiseData:any;
  districtData:any;
  ngOnInit(): void {
    this.getGloablData();
    this.getAllCountryData();
    this.getIndiaData();
  }

  getGloablData() {
    return this.utility.getGlobalData().subscribe((resp:any) => {
      console.log(resp);
      this.totalCases = resp.cases;
      this.totalDeaths = resp.deaths;
      this.totalRecoverd = resp.recovered;
    });
  }

  getAllCountryData(){
    return this.utility.getCountryData().subscribe((resp:any) => {
      this.allCountry.push(resp);
      resp.filter((respArry:any) => {
        if( respArry.country == 'India') {
          console.log(respArry);
          this.indiaData = respArry;
          this.recoverPer = Math.trunc((this.indiaData.recovered/this.indiaData.cases) * 100);
          this.deathPer = Math.trunc((this.indiaData.deaths/this.indiaData.cases) * 100);
        }
      })

    })
  }

  getDistrict(state:string){
    return this.utility.getDistrict().subscribe((resp:any) => {
      //console.log(state);
      for (const key in resp) {
        if (resp.hasOwnProperty(state)) {
          const element = resp[state];
          if(key == state){
            for (const dkey in element) {
              if (element.hasOwnProperty(dkey)) {
                const district = element[dkey];
                this.districtData = Object.entries(district)
              }
            }
          }
        }
      }
      console.log(this.districtData);
    })
  }

  getIndiaData(){
    return this.utility.getIndiaData().subscribe((resp:any) => {
      console.log(resp.statewise);
      this.indiaTotalCases = resp.statewise[0];
      this.statewiseData = resp.statewise;
      this.statewiseData.shift();
    })
  }
}
